<template>
  <div class="panelform" @scroll="getscroll($event)">
    <el-table
    :data="this.$store.state.tableData"
    :row-class-name="tableRowClassName"
    
    style="width: 100%">
    <el-table-column v-for="item in this.$store.state.lineName" :key="item" :prop="item" :label="item" >
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
import store from '../../store/index'
import {request} from '../../network/request'


export default {
  name: 'panelform',
  data(){
    return{
      //  tableData: [{
      //     date: '2016-05-02',
      //     name: '王小虎',
      //     address: '上海市普陀区金沙江路 1518 弄'
      //   }, {
      //     date: '2016-05-04',
      //     name: '王小虎',
      //     address: '上海市普陀区金沙江路 1517 弄'
      //   }, {
      //     date: '2016-05-01',
      //     name: '王小虎',
      //     address: '上海市普陀区金沙江路 1519 弄'
      //   }, {
      //     date: '2016-05-03',
      //     name: '王小虎',
      //     address: '上海市普陀区金沙江路 1516 弄'
      //   }, ]
      page:0,
    }
  },
  methods:{
    tableRowClassName({row, rowIndex}) {
      for (let index = 0; index < this.$store.state.warnrow.length; index++) {
         if (rowIndex==this.$store.state.warnrow[index]) {
          console.log('success');
          return 'warning-row';
        }
      }
        // console.log(rowIndex);
        return '';
      },

      getscroll(event) {
      // 滚动条距离底部的距离scrollBottom
        var scrollBottom =
          event.target.scrollHeight -
          event.target.scrollTop -
          event.target.clientHeight;
        if (scrollBottom ==0) {
          this.page+=1;         
          request({
              url:'/ws/fields',
              params:{
                checkedTable:this.$store.state.currentName,
                page:this.page
              },
          },res=>{
              //success函数
              console.log(res);
              if(res.data.value){
                for (let index = 0; index < res.data.value.length; index++) {
                  this.$store.state.tableData.push(res.data.value[index])
                }
                console.log(this.$store.state.tableData);
                event.target.scrollTop=0.5*event.target.scrollHeight;
              }
          },err=>{
              //failure函数
              console.log(err);
          })
          console.log("bottom");
        }
      },
  }
}
</script>

<style>
  .panelform{
    height: 100%; 
    overflow: auto;
  }

  .el-table .warning-row {
    background: oldlace;
  }
</style>
