<template>
   <div class="maingraph">
        <div class="leftbar">
        <div class="height">
        <draggable v-model="IdList" :options="{group:{name:'task',pull:'clone'},sort: true}" animation="300" :move="onMove">
            <transition-group>
            <div class="item" v-for="(item,index) in IdList" :key="index" >{{item.COLUMN_NAME}}</div>
            </transition-group>
        </draggable> 
        </div>
        <div class="content">
                <div class="width">
            <draggable v-model="xaxis" group="task" filter=".forbid" animation="300" @add='add1' :move="onMove">
                <transition-group class="trgroup">
                    <div v-for="(item,index) in xtitle" :key="index" class="item forbid" style="width:50px">{{item.xname}}</div>
                    <div v-for="(item,index) in xtitle" :key="index" class="item forbid" style="width:0px"></div>
                    <div v-for="(item,index) in xaxis" :key="index" class="axis"><el-tag closable :disable-transitions="false"  @close="xhandleClose(item)">{{item.COLUMN_NAME}}</el-tag></div>
                </transition-group>
            </draggable> 
            </div>
            <div class="width">
            <draggable v-model="yaxis" group="task" animation="300" @add='add1' :move="onMove">
                <transition-group class="trgroup">
                    <div v-for="(item,index) in ytitle" :key="index" class="item forbid" style="width:50px">{{item.yname}}</div>
                    <div v-for="(item,index) in ytitle" :key="index" class="item forbid" style="width:0px"></div>
                    <div v-for="(item,index) in yaxis" :key="index" class="axis"><el-tag closable :disable-transitions="false"  @close="yhandleClose(item)">{{item.COLUMN_NAME}}</el-tag></div>
                </transition-group>
            </draggable> 
            </div>
            <div class="graph" id="areagraph" v-show="areagraph" style="background-color: #fff; width: 88%; height: 70%;"></div>
            <div class="graph" id="bargraph" v-show="bargraph" style="background-color: #fff; width: 88%; height: 70%"></div>
            <div class="graph" id="linegraph" v-show="linegraph" style="background-color: #fff; width: 88%; height: 70%"></div>
            <div class="graph" id="piegraph" v-show="piegraph" style="background-color: #fff; width: 88%; height: 70%"></div>
        </div>
        </div>
        <div class="rightbar">
            <p>图表标题</p>
            <input type="text" placeholder="未命名图表">
            <p>图表类型</p>
            <div class="graphkind">
                 <el-popover trigger="hover" placement="left">
                    <p class="kindname">面积折线图</p>
                    <p class="kindinfo">一个维度</p>
                    <p class="kindinfo">一个数值或多个数值</p>     
                    <div slot="reference" class="name-wrapper">
                        <i class="fa fa-area-chart" aria-hidden="true" :style="{ background: activeColor1, border: borderColor1 }" @click="active1"></i>
                    </div>
                 </el-popover>
                  <el-popover trigger="hover" placement="left">
                    <p class="kindname">柱状图</p>
                    <p class="kindinfo">一个维度</p>
                    <p class="kindinfo">一个数值</p>
                    <div slot="reference" class="name-wrapper">
                        <i class="fa fa-bar-chart" aria-hidden="true" :style="{ background: activeColor2, border: borderColor2 }" @click="active2"></i>
                    </div>
                 </el-popover>
                  <el-popover trigger="hover" placement="left">
                    <p class="kindname">折线图</p>
                    <p class="kindinfo">一个维度</p>
                    <p class="kindinfo">一个数值</p>
                    <div slot="reference" class="name-wrapper">
                         <i class="fa fa-line-chart" aria-hidden="true" :style="{ background: activeColor3, border: borderColor3 }" @click="active3"></i>
                    </div>
                 </el-popover>
                  <el-popover trigger="hover" placement="left">
                    <p class="kindname">饼图</p>
                    <p class="kindinfo">一个维度</p>
                    <p class="kindinfo">一个数值</p>
                    <div slot="reference" class="name-wrapper">
                        <i class="fa fa-pie-chart" aria-hidden="true" :style="{ background: activeColor4, border: borderColor4 }" @click="active4"></i>
                    </div>
                 </el-popover>
            </div>
            <div class="forecast">
                <div style="margin-left: 5px; margin-bottom: 20px"><span>数据智能分析</span></div>
                <el-upload
                class="upload-demo"
                accept=".py,.cpp"
                drag
                action="http://47.102.146.116:8080/dsh/upload"
                :data="upload"
                :on-success="success"
                style="width:15.625rem"
                multiple>
                <div class="el-upload-dragger" style="width:15.625rem">
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text" style="width:15.625rem">将文件拖到此处，或<em>点击上传</em></div>
                </div>
                </el-upload>
                <div @click="showfuncchart" class="submit" style="font-size:12px"><span>结果展示</span></div>
            </div>
            <div class="submit" @click="submit" style="margin-top: 40px"><router-link to="/board" style="text-decoration: none; color: #5182e4;">保存图表</router-link></div>
        </div>
        <div class="func_chart" id="func_chart" v-show="this.$store.state.showFuncChart">
        </div>
   </div>
</template>

<script>
import draggable from 'vuedraggable'
import store from '../../store/index'
import {request, requestandset} from '../../network/request'
var echarts = require('echarts');
export default {
  name: 'maingraph',
  data(){
      return{
        activeColor1:'',
        activeColor2:'',
        activeColor3:'',
        activeColor4:'',
        borderColor1:'',
        borderColor2:'',
        borderColor3:'',
        borderColor4:'',

        areagraph:false,
        bargraph:false,
        linegraph:false,
        piegraph:false,

        chart_type:'',

        table_name:'',
        xtitle:[{xname:"维度"}],
        ytitle:[{yname:"数值"}],
        IdList:[],
        xaxis:[],
        yaxis:[],

        //测试文件传输
        dimension:'',
        number:'',

        moveId:-1,

        areaoption : {
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: []
            },
            yAxis: {
                type: 'value'
            },
            series:[ {
                data: [],
                type: 'line',
                areaStyle: {}
            }]
        },
        lineoption : {
            xAxis: {
                type: 'category',
                data: []
            },
            yAxis: {
               type: 'value'
            },
            series: {
                data: [],
                type: 'line',
                smooth: true
             }
            },
        baroption : {
            xAxis: {
                data: []
            },
            yAxis: {},
            series: {
                name: '销量',
                type: 'bar',
                data: []
            }
        },
        pieoption : {
               visualMap: {
                show: false,
                min: 80,
                max: 600,
                inRange: {
                    colorLightness: [0, 1]
                }
            },
            series: {
                    name:'',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '50%'],
                    data: [
                        // {value: 335, name: '直接访问'},
                        // {value: 310, name: '邮件营销'},
                        // {value: 274, name: '联盟广告'},
                        // {value: 235, name: '视频广告'},
                        // {value: 400, name: '搜索引擎'}
                    ],
                    roseType: 'radius',
                    label: {
                        color: 'rgba(0, 0, 0, 0.6)'
                    },
                    labelLine: {
                        lineStyle: {
                            color: 'rgba(0, 0, 0, 0.6)'
                        },
                        smooth: 0.2,
                        length: 10,
                        length2: 20
                    },
                    itemStyle: {
                        color: '#c23531',
                        shadowBlur: 200,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    },
                    animationType: 'scale',
                    animationEasing: 'elasticOut',
                    animationDelay: function (idx) {
                        return Math.random() * 200;
                    }
                }   
        },
      
       sysdimension:[],
       sysnumber:[]
      
      }
  },
  components:{
      draggable,
      store,
  },
  created(){
      request({
        url:'/dsh/field',
        params: {
            table_name:this.$store.state.table_name
        }
      },res=>{
          //success函数
          console.log(res);
          this.IdList=res.data;
      },err=>{
          //failure函数
          console.log(err);
      })
  },
  computed:{
      upload(){
          return {
              tableName:this.$store.state.table_name,
              dimension:this.dimension,
              number:this.number
          }
      }
  },
    methods: {
        active1(){
            this.activeColor1='white'
            this.borderColor1='2px solid #5597fa'
            this.activeColor2=''
            this.borderColor2=''
            this.activeColor3=''
            this.borderColor3=''
            this.activeColor4=''
            this.borderColor4=''
            this.areagraph=true
            this.bargraph=false
            this.linegraph=false
            this.piegraph=false
            this.chart_type='area'
             this.areaoption.xAxis.data.splice(0)
                this.areaoption.series.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    // console.log(res);
                    for (var key in res.data.dimension){
                        this.areaoption.xAxis.data=res.data.dimension[key]
                        break
                    }
                    for (var key in res.data.number){
                        var areaseries={
                            data: [],
                            type: 'line',
                            areaStyle: {}
                        }
                        areaseries.data=res.data.number[key]
                        this.areaoption.series.push(areaseries)
                        // break
                    }
                    console.log(this.areaoption.xAxis.data);
                    console.log(this.areaoption.series);
                },res=>{
                    //success函数
                    console.log(res);
                    if (areaChart && areaChart.dispose) {
                        areaChart.dispose();
                    }
                    var areaChart = null;
                    var div_ = document.getElementById("areagraph");
                    div_.removeAttribute("_echarts_instance_");
                    areaChart = echarts.init(div_);
                    // areaChart.clear();
                    areaChart.setOption(this.areaoption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
        },
        active2(){
            this.activeColor2='white'
            this.borderColor2='2px solid #5597fa'
            this.activeColor1=''
            this.borderColor1=''
            this.activeColor3=''
            this.borderColor3=''
            this.activeColor4=''
            this.borderColor4=''
            this.areagraph=false
            this.bargraph=true
            this.linegraph=false
            this.piegraph=false
            this.chart_type='bar'
             this.baroption.xAxis.data.splice(0)
                this.baroption.series.data.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    for (var key in res.data.dimension){
                        this.baroption.xAxis.data=res.data.dimension[key]
                        break
                    }
                    for (var key in res.data.number){
                        this.baroption.series.data=res.data.number[key]
                        break
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    if (barChart && barChart.dispose) {
                        barChart.dispose();
                    }
                    var barChart = null;
                    var div_ = document.getElementById("bargraph");
                    div_.removeAttribute("_echarts_instance_");
                    barChart = echarts.init(div_);
                    // areaChart.clear();
                    barChart.setOption(this.baroption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
        },
        active3(){
            this.activeColor3='white'
            this.borderColor3='2px solid #5597fa'
            this.activeColor2=''
            this.borderColor2=''
            this.activeColor1=''
            this.borderColor1=''
            this.activeColor4=''
            this.borderColor4=''
            this.areagraph=false
            this.bargraph=false
            this.linegraph=true
            this.piegraph=false
            this.chart_type='line'
            this.lineoption.xAxis.data.splice(0)
                this.lineoption.series.data.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    for (var key in res.data.dimension){
                        this.lineoption.xAxis.data=res.data.dimension[key]
                        break
                    }
                    for (var key in res.data.number){
                        this.lineoption.series.data=res.data.number[key]
                        break
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    if (lineChart && lineChart.dispose) {
                        lineChart.dispose();
                    }
                    var lineChart = null;
                    var div_ = document.getElementById("linegraph");
                    div_.removeAttribute("_echarts_instance_");
                    lineChart = echarts.init(div_);
                    // areaChart.clear();
                    lineChart.setOption(this.lineoption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
        },
        active4(){
            this.activeColor4='white'
            this.borderColor4='2px solid #5597fa'
            this.activeColor2=''
            this.borderColor2=''
            this.activeColor3=''
            this.borderColor3=''
            this.activeColor1=''
            this.borderColor1=''
            this.areagraph=false
            this.bargraph=false
            this.linegraph=false
            this.piegraph=true
            this.chart_type='pie'
             this.pieoption.series.data.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    this.pieoption.series.data.splice(0)
                    console.log(this.pieoption.series.data);
                    // for (var key in res.data.dimension){
                    //     this.areaoption.xAxis.data=res.data.dimension[key]
                    //     break
                    // }
                    // for (var key in res.data.number){
                    //     this.areaoption.series.data=res.data.number[key]
                    //     break
                    // }
                    // console.log(Object.keys(res.data.dimension).length);
                    // console.log(Object.keys(res.data.number).length);
                    if (Object.keys(res.data.dimension).length&&Object.keys(res.data.number).length) {
                        var dimension=[]
                        var number=[]
                        for (var key in res.data.dimension){
                            dimension=res.data.dimension[key]
                            break
                        }
                        for (var key in res.data.number){
                            number=res.data.number[key]
                            break
                        }
                        for (let index = 0; index < dimension.length; index++) {
                            this.pieoption.series.data.push({value:number[index], name:dimension[index]})
                        }
                    }
                    console.log('test');
                    console.log(this.pieoption.series.data);
                },res=>{
                    //success函数
                    console.log(res);
                    if (pieChart && pieChart.dispose) {
                        pieChart.dispose();
                    }
                    var pieChart = null;
                    var div_ = document.getElementById("piegraph");
                    div_.removeAttribute("_echarts_instance_");
                    pieChart = echarts.init(div_);
                    // areaChart.clear();
                    pieChart.setOption(this.pieoption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
        },  
        add1(){
            this.dimension=this.xaxis[0].COLUMN_NAME
            this.number=this.yaxis[0].COLUMN_NAME
            if (this.areagraph) {
                this.areaoption.xAxis.data.splice(0)
                this.areaoption.series.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    // console.log(res);
                    for (var key in res.data.dimension){
                        this.areaoption.xAxis.data=res.data.dimension[key]
                        break
                    }
                    for (var key in res.data.number){
                        var areaseries={
                            data: [],
                            type: 'line',
                            areaStyle: {}
                        }
                        areaseries.data=res.data.number[key]
                        this.areaoption.series.push(areaseries)
                        // break
                    }
                    console.log(this.areaoption.xAxis.data);
                    console.log(this.areaoption.series);
                },res=>{
                    //success函数
                    console.log(res);
                    if (areaChart && areaChart.dispose) {
                        areaChart.dispose();
                    }
                    var areaChart = null;
                    var div_ = document.getElementById("areagraph");
                    div_.removeAttribute("_echarts_instance_");
                    areaChart = echarts.init(div_);
                    // areaChart.clear();
                    areaChart.setOption(this.areaoption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
                
            }
            else if (this.bargraph) {
                this.baroption.xAxis.data.splice(0)
                this.baroption.series.data.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    for (var key in res.data.dimension){
                        this.baroption.xAxis.data=res.data.dimension[key]
                        break
                    }
                    for (var key in res.data.number){
                        this.baroption.series.data=res.data.number[key]
                        break
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    if (barChart && barChart.dispose) {
                        barChart.dispose();
                    }
                    var barChart = null;
                    var div_ = document.getElementById("bargraph");
                    div_.removeAttribute("_echarts_instance_");
                    barChart = echarts.init(div_);
                    // areaChart.clear();
                    barChart.setOption(this.baroption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
            }
            else if (this.linegraph) {
                this.lineoption.xAxis.data.splice(0)
                this.lineoption.series.data.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    for (var key in res.data.dimension){
                        this.lineoption.xAxis.data=res.data.dimension[key]
                        break
                    }
                    for (var key in res.data.number){
                        this.lineoption.series.data=res.data.number[key]
                        break
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    if (lineChart && lineChart.dispose) {
                        lineChart.dispose();
                    }
                    var lineChart = null;
                    var div_ = document.getElementById("linegraph");
                    div_.removeAttribute("_echarts_instance_");
                    lineChart = echarts.init(div_);
                    // areaChart.clear();
                    lineChart.setOption(this.lineoption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
            }
            else if (this.piegraph) {
                this.pieoption.series.data.splice(0)
                requestandset({
                    url:'/dsh/field_data',
                    method:'post',
                    data: {
                        dimension:this.xaxis,
                        number:this.yaxis,
                        table_name:this.$store.state.table_name
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    this.pieoption.series.data.splice(0)
                    console.log(this.pieoption.series.data);
                    // for (var key in res.data.dimension){
                    //     this.areaoption.xAxis.data=res.data.dimension[key]
                    //     break
                    // }
                    // for (var key in res.data.number){
                    //     this.areaoption.series.data=res.data.number[key]
                    //     break
                    // }
                    // console.log(Object.keys(res.data.dimension).length);
                    // console.log(Object.keys(res.data.number).length);
                    if (Object.keys(res.data.dimension).length&&Object.keys(res.data.number).length) {
                        var dimension=[]
                        var number=[]
                        for (var key in res.data.dimension){
                            dimension=res.data.dimension[key]
                            break
                        }
                        for (var key in res.data.number){
                            number=res.data.number[key]
                            break
                        }
                        for (let index = 0; index < dimension.length; index++) {
                            this.pieoption.series.data.push({value:number[index], name:dimension[index]})
                        }
                    }
                    console.log(this.pieoption.series.data);
                },res=>{
                    //success函数
                    console.log(res);
                    if (pieChart && pieChart.dispose) {
                        pieChart.dispose();
                    }
                    var pieChart = null;
                    var div_ = document.getElementById("piegraph");
                    div_.removeAttribute("_echarts_instance_");
                    pieChart = echarts.init(div_);
                    // areaChart.clear();
                    pieChart.setOption(this.pieoption);
                },err=>{
                    //failure函数
                    console.log(err);
                })
            }
        },

        onMove(e) {
                    // 不允许停靠
                    if (e.relatedContext.index == 0) return false;
                    return true;
                },

       xhandleClose(item) {
            this.xaxis.splice(this.xaxis.indexOf(item), 1);
        },
        yhandleClose(item) {
            this.yaxis.splice(this.yaxis.indexOf(item), 1);
        },

        showfuncchart(){
            this.$store.commit('funcChart');
            var option = {
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: this.sysdimension//x轴
                },
                yAxis: {
                    type: 'value'
                },
                series: [{
                    data: this.sysnumber,//y轴
                    type: 'line',
                }],
                itemStyle: {
                        color: '#5182e4'
                }
            };
            echarts.init(document.getElementById('func_chart')).setOption(option)
        },

        submit(){
            request({
                url:'/dsh/create',
                method:'post',
                data:{
                    dimension:this.xaxis,
                    number:this.yaxis,
                    table_name:this.$store.state.table_name,
                    chart_type:this.chart_type,
                    dashName:this.$store.state.boardname
                }
            },res=>{
                console.log(res);
            },err=>{
                console.log(err);
            })
        },

        success(response, file, fileList){
            console.log(response);
            this.sysdimension=response.dimension;
            this.sysnumber=response.number
        }
  },
};
</script>

<style>

    .maingraph .upload-demo{
        width: 15.625rem!important;
    }

    .maingraph .el-upload--text{
        width: 15.625rem!important;
    }
    .maingraph .el-upload-dragger{
        width: 15.625rem!important;
    }

    .maingraph .item{
        cursor: pointer;
        height: 1.25rem;
        line-height: 1.25rem;
        width: 81%;
        padding: 10px 0;
        padding-left: 25px;
        margin: 0;
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 12px;
        font-weight: 600;
        color: rgba(10,18,32,.64);
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-rendering: optimizelegibility;
    }

    .trgroup{
        display: flex;
        flex-direction: row;
