<template>
    <div class="mask">
        <div class="dialog">
            <p>添加图表</p>
            <p>选择工作表</p>
            <div style="width: 250px;height: 100px; overflow-y:auto; overflow-x:hidden;">
               <form>
                   <label :for="item.nv_tb_name" v-for= "item in worksheet" :key="item.nv_tb_name" class="worksheetselect" @click="$store.state.table_name=item.nv_tb_name" style="display: block">
                       <input name="worksheet" type="radio" :id="item.nv_tb_name">{{item.nv_tb_name}}
                    </label>
               </form>
            </div>
            <router-link to="/board/draw" class="next">确定</router-link>
        </div>
    </div>
</template>
<script>
    import store from '../store/index'
    import {request} from '../network/request'
    export default {
        name:'boardmask',
        data(){
            return{
                worksheet:[],
            }
        },

        created(){
            request({
                url:'/dsh/table',
            },res=>{
                //success函数
                console.log(res);
                this.worksheet=res.data;
            },err=>{
                //failure函数
                console.log(err);
            })
        },
        components:{
            store,
        },
        
        methods: {

        },
    }
</script>
<style>
    .mask{
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        background-color: rgba(184, 184, 184, 0.95);
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .dialog{
        background-color: #fff;
        padding: 20px;
        border-radius: 2px;
        width: 18.75rem;
        height: 12.5rem;
    }

    .worksheetselect{
        display: block;
    }

    .mask .next{
        text-decoration: none;
        color: #5182e4;
        font-weight: 550;
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 15px;
        padding: 5px;
    }
    .mask .next:hover{
        background:rgba(81,130,228,0.1);
    }

    .mask label{
        white-space: nowrap;  /*强制span不换行*/
        display: inline-block;  /*将span当做块级元素对待*/
        overflow: hidden;  /*超出宽度部分隐藏*/
        text-overflow: ellipsis;  /*超出部分以点号代替*/
    }
</style>