<template>
  <div class="panel" ref="panel">
    <div class="box">
      <sidebar class="sidebar" ref="sidebar">
        <div class="sheet" slot="name">
          <span>仪表盘</span>
          <i class="fa fa-plus" aria-hidden="true" style="margin-left: 80px; cursor: pointer;" @click="addboard=!addboard"></i>
          <div class="addboard" v-show="addboard">
            <input id="boardname" type="text" placeholder="请输入仪表盘名称" style="width: 120px;" v-model="addboardname"><br>
            <input type="sbumit" value="确定" class="submit" style="width: 50px; margin-left: 90px" @click="addboardfun">
          </div>
        </div>
        
        <p v-for= "(item, index) in boardlist" :key="index" class="item" :class="{active:boardname == item}" slot="item" @click='selectBoard(item)'>{{item}}<i class="fa fa-trash-o" aria-hidden="true" @click="deleteboard(index)"></i></p>
      </sidebar>

  <div class="separator" ref="separator">
      <div class="separatoritem"></div>
  </div>
  </div>
     <graph class="graph" ref="graph">
        <div slot="name">
          <span class="graphname">{{boardname}}</span>
          <div class="addgraphbutton">
            <router-link to="/board/mask"><i class="fa fa-file-image-o"></i>添加图表</router-link>
          </div>
        </div>
        <router-link to="/board/mask" tag="div" class="add" slot="graphs" v-if="this.graphnum==0">
          <i class="row"></i>
          <i class="column"></i>
          <p class="info">添加图表</p>
        </router-link>

        <div class="smallgraphs" slot="graphs" v-for="(index, count) in graphnum" :key="count" :id="index-1" tabindex="0" hidefocus="true"></div>
     </graph>
  </div>
</template>

<script>
import sidebar from '../components/panel/sidebar'
import graph from'../components/panel/graph'
import store from '../store/index'
import {request, requestandset} from '../network/request'
var echarts = require('echarts');
export default {
  name: 'board',
  data(){
    return{
        addboard:0,
        addboardname:'',
        boardlist:[],
        boardname:'仪表盘',
        graphnum:0,
        options:[],
        areaoption : {
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: []
            },
            yAxis: {
                type: 'value'
            },
            series: {
                data: [],
                type: 'line',
                areaStyle: {}
            }
        },
        lineoption : {
            xAxis: {
                type: 'category',
                data: []
            },
            yAxis: {
               type: 'value'
            },
            series: {
                data: [],
                type: 'line',
                smooth: true
             }
            },
        baroption : {
            xAxis: {
                data: []
            },
            yAxis: {},
            series: {
                name: '销量',
                type: 'bar',
                data: []
            }
        },
        pieoption : {
               visualMap: {
                show: false,
                min: 80,
                max: 600,
                inRange: {
                    colorLightness: [0, 1]
                }
            },
            series: {
                    name:'',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '50%'],
                    data: [
                        // {value: 335, name: '直接访问'},
                        // {value: 310, name: '邮件营销'},
                        // {value: 274, name: '联盟广告'},
                        // {value: 235, name: '视频广告'},
                        // {value: 400, name: '搜索引擎'}
                    ],
                    roseType: 'radius',
                    itemStyle: {
                        color: '#c23531',
                    },
                    animationType: 'scale',
                    animationEasing: 'elasticOut',
                    animationDelay: function (idx) {
                        return Math.random() * 200;
                    }
                }   
        },
    }
  },
  components:{
      sidebar,
      graph,
      store,
  },
  created(){
      request({
        url:'/dsh/dshName',
      },res=>{
          //success函数
          console.log(res);
          this.boardlist=res.data;
      },err=>{
          //failure函数
          console.log(err);
      })
  },
  mounted () {
      this.dragControllerDiv();
    },
    methods: {
        addboardfun(){
          this.addboard=!this.addboard;
          this.boardlist.push(this.addboardname);
          request({
            url:'/dsh/addDash',
            params:{
                newDash:this.addboardname
            }
          },res=>{
              //success函数
              console.log(res);
          },err=>{
              //failure函数
              console.log(err);
          })
        },
        setOption(){
          for (var option in this.options) {
            echarts.init(document.getElementById(option)).dispose()
            echarts.init(document.getElementById(option)).setOption(this.options[option])
          }
        },
        selectBoard(boardname){
          this.boardname=boardname;
          this.$store.state.boardname=boardname;
          this.options.splice(0,this.options.length);
          requestandset({
              url:'/dsh/show',
              params:{
                dashName:this.boardname
              }
            },res=>{
                //success函数
                console.log(res);
                this.graphnum=res.data.length;
                for (var chart in res.data) {
                  if (res.data[chart].type=='area') {
                     var areaoption = {
                        toolbox: {
                            feature: {
                                restore: {},
                                saveAsImage: {}
                            }
                        },
                        xAxis: {
                            type: 'category',
                            boundaryGap: false,
                            data: []
                        },
                        yAxis: {
                            type: 'value'
                        },
                        series: [

                        ],
                        grid: {
                          containLabel: true
                        }
                      };
                      for (var item in res.data[chart].dimension) {
                        areaoption.xAxis.data=res.data[chart].dimension[item];
                      }
                      for (var item in res.data[chart].number) {
                        var areaseries={
                                data: [],
                                type: 'line',
                                areaStyle: {}
                            }
                            areaseries.data=res.data[chart].number[item]
                            areaoption.series.push(areaseries)
                      }
                      this.options.push(areaoption);
                  }
                  if (res.data[chart].type=='line') {
                   var lineoption = {
                        toolbox: {
                            feature: {
                                restore: {},
                                saveAsImage: {}
                            }
                        },
                      xAxis: {
                          type: 'category',
                          data: []
                      },
                      yAxis: {
                        type: 'value'
                      },
                      series: {
                          data: [],
                          type: 'line',
                          smooth: true
                      },
                      grid: {
                          containLabel: true
                      }
                    };
                    for (var item in res.data[chart].dimension) {
                      lineoption.xAxis.data=res.data[chart].dimension[item];
                    }
                    for (var item in res.data[chart].number){
                      lineoption.series.data=res.data[chart].number[item];
                    }
                    this.options.push(lineoption);
                 }
                  if (res.data[chart].type=='bar'){
                    var baroption = {
                         toolbox: {
                            feature: {
                                restore: {},
                                saveAsImage: {}
                            }
                        },
                        xAxis: {
                            data: []
                        },
                        yAxis: {},
                        series: {
                            name: '销量',
                            type: 'bar',
                            data: []
                        },
                        grid: {
                          containLabel: true
                        }
                    };
                     for (var item in res.data[chart].dimension) {
                      baroption.xAxis.data=res.data[chart].dimension[item];
                    }
                    for (var item in res.data[chart].number){
                      baroption.series.data=res.data[chart].number[item];
                    }
                    this.options.push(baroption);
                  }
                }
            },res=>{
                //success函数
                this.setOption();
            },err=>{
                //failure函数
                console.log(err);
          })
        },
        dragControllerDiv: function () {
            var resize = document.getElementsByClassName('separator');
            var left = document.getElementsByClassName('sidebar');
            var mid = document.getElementsByClassName('graph');
            var box = document.getElementsByClassName('panel');
            for (let i = 0; i < resize.length; i++) {
            // 鼠标按下事件
            resize[i].onmousedown = function (e) {
                var startX = e.clientX;
                resize[i].left = resize[i].offsetLeft;
                // 鼠标拖动事件
                document.onmousemove = function (e) {
                var endX = e.clientX;
                var moveLen = resize[i].left + (endX - startX); // （endx-startx）=移动的距离。resize[i].left+移动的距离=左边区域最后的宽度
                var maxT = box[i].clientWidth - resize[i].offsetWidth;  // 容器宽度 - 左边区域的宽度 = 右边区域的宽度
    
                if (moveLen < 200) moveLen = 200; // 左边区域的最小宽度为150px
                if (moveLen > maxT - 200) moveLen = maxT  - 200;  //右边区域最小宽度为150px
    
                resize[i].style.left = moveLen; // 设置左侧区域的宽度
    
                for (let j = 0; j < left.length; j++) {
                    left[j].style.width = moveLen + 'px';
                    mid[j].style.width = (box[i].clientWidth - moveLen - 10) + 'px';
                }
                }
                // 鼠标松开事件
                document.onmouseup = function (evt) {
                document.onmousemove = null;
                document.onmouseup = null;
                resize[i].releaseCapture && resize[i].releaseCapture(); //当你不在需要继续获得鼠标消息就要应该调用ReleaseCapture()释放掉
                }
                resize[i].setCapture && resize[i].setCapture(); //该函数在属于当前线程的指定窗口里设置鼠标捕获
                return false;
            }
            }
        },
        deleteboard(index){
          console.log(index);
          location.reload();
           request({
              url:'/dsh/delete',
              params:{
                dashName:this.boardname
              },
            },res=>{
                //success函数
                console.log(res);
            },err=>{
                //failure函数
                console.log(err);
            })
        }
    }
}
</script>

<style>

  .panel .fa-trash-o{
    display: block;
    float: right;
    margin-right: 3px;
  }

  .panel .fa-trash-o:hover{
    color: #5182E4;
  }

  .panel{
    height: 100%;
    display: flex;
    flex-direction: row;
  }

  .panel .addboard{
    background-color: #fff;
    box-shadow: 0 0 6px 0 rgba(0,0,0,.12), 0 10px 12px 0 rgba(170,182,206,.2), inset 0 -1px 0 0 rgba(255,255,255,.3);
  }

  .panel .submit{
    line-height: 20px;
    border-bottom: none;
    text-align: center;
    cursor: pointer;
    
  }

  .panel .submit:hover{
    background-color:rgba(81,130,228,0.1);
    transition: 0.7s all;
  }

  .graph{
    flex: 1;
    overflow: auto;
  }

  .panel .box{
     position: relative;
  }


  .panel .box .separator {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 1px;
    right: 0;
    width: 14px;
    height: 100%;
    background-color: white;
    cursor: col-resize;
    border-radius: 2px;
  }
  
  .panel .box .separator .separatoritem {
    display: inline-block;
    height: 20px;
    width: 6px;
    background-color: #e9e9e9;
    margin: 0 1px;
    border-radius: 5px;
  }
  
  .panel .box .separator:hover{
    background-color: #fafbfc;
  }

  .sheet{
    font-size: 13px;
		font-weight: 600;
		font-family: sans-serif;
    color: #a1a1a1;
    padding: 10px;
  }

    .panel .item{
    cursor: pointer;
    height: 1.25rem;
    width: 81%;
    padding: 10px 0;
    padding-left: 25px;
    margin: 0;
  }

  .graphname{
    display: inline-block;
    font-size: 22px;
    font-family: Roboto,'PingFang SC',sans-serif!important;
    margin: 30px;
    color: rgba(10,18,32,.64);
  }

  .addgraphbutton{
        display: inline-block;
        position: relative;
        color: rgba(10,18,32,.64);
        text-decoration: none;
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 15px;
        font-weight: 400;
        margin-left: 50px;
        width: 75px;
  }

  .addgraphbutton:hover{
    color: rgba(10,18,32,.64);
  }

  .addgraphbutton:before{
			content: '';
			position: absolute;
			width: 0;
			left: 50%;
      top: 23px;
			bottom: 0;
			height: 1px;
			background-color: #5182E4;
		}

		.addgraphbutton:hover:before{
			width: 100%;
			left: 0%;
			transition: all 0.5s;
		}

  .fa-file-image-o{
    color: #5182E4;
    margin-right:5px;
  }

  .add{
      background-color: #fff;
      width: 18.75rem;
      height: 14.5rem;
      margin-left: 30px;
      align-items: center;
      position: relative;
  }

  .add:hover{
    transition: 0.7s all;
     box-shadow: 0 0 6px 0 rgba(0,0,0,.12), 0 10px 12px 0 rgba(170,182,206,.2), inset 0 -1px 0 0 rgba(255,255,255,.3);
  }

  .add .row{
    position: absolute;
    top: 99px;
    left: 120px;
    height: 2px;
    width: 60px;
    background-color: #000;
  }

  .add .column{
    position: absolute;
    top: 70px;
    left: 149px;
    height: 60px;
    width: 2px;
    background-color: #000;
  }

  .add .info{
    position: absolute;
    top: 130px;
    left: 119px;
  }

  .smallgraphs{
    display: inline-block;
    width: 28.75rem;
    height: 24.5rem;
    margin-left: 30px;
    background-color: #fff;
  }

  .smallgraphs:hover{
    transition: 0.7s all;
     box-shadow: 0 0 6px 0 rgba(0,0,0,.12), 0 10px 12px 0 rgba(170,182,206,.2), inset 0 -1px 0 0 rgba(255,255,255,.3);
  }
</style>