import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    table_name:'',
    showdbSelect:0,//决定显示选择数据库界面还是数据库详情页
    visited:1,
    tableData:[],
    lineName:[],
    ckeckedTable:[],
    warnrow:[],
    showFuncChart:0,
    boardname:'',
    currentName:'',
    },
  mutations: {
    showselect(state){state.showdbSelect=0},
    showview(state){state.showdbSelect=1},
    visitedcontrol(state){state.visited=0},
    funcChart(state){state.showFuncChart=1},
  },
  actions: {
  },
  modules: {
  }
})

export default store