import Vue from 'vue'
import VueRouter from 'vue-router'
import Panel from '../views/panel.vue'
import Board from '../views/board.vue'
import Boardmask from '../views/boardmask.vue'
import Drawgraph from '../views/drawgraph.vue'
import Database from '../views/database.vue'
import Oracle from '../components/database/oracle.vue'
import mySQL from '../components/database/mySQL.vue'
import excle from '../components/database/excle.vue'
import FormConnect from '../views/formconnect.vue'
import Alert from '../components/panel/alertmask.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/panel',
    name: 'Panel',
    component: Panel
  },
  {
    path: '/board',
    name: 'Board',
    component: Board
  },
  {
    path: '/database',
    name: 'Database',
    component: Database
  },
  {
    path: '/board/mask',
    name: 'Boardmask',
    component: Boardmask
  },
  {
    path: '/board/draw',
    name: 'Drawgraph',
    component: Drawgraph
  },
  {
    path: '/database/oracle',
    name: 'Oracle',
    component: Oracle
  },
  {
    path: '/database/mySQL',
    name: 'mySQL',
    component: mySQL
  },
  {
    path: '/database/excle',
    name: 'excle',
    component: excle
  },
  {
    path: '/panel/formconnect',
    name: 'FormConnect',
    component: FormConnect
  },
  {
    path: '/panel/alert',
    name: 'Alert',
    component: Alert
  },
]

const router = new VueRouter({
  routes
})

export default router
