import axios from 'axios'



export function request(config,success,failure){
    const instance =axios.create({
        baseURL:'http://139.196.101.174:8080',
        timeout:10000,
    })

    instance(config)
    .then(res=>{
        success(res)
    })
    .catch(err=>{
        failure(err)
    })
}


export function requestandset(config,success,set,failure){
    const instance =axios.create({
        baseURL:'http://139.196.101.174:8080',
        timeout:10000,
    })

    instance(config)
    .then(res=>{
        success(res)
    })
    .then(res=> {
        set(res)
    })
    .catch(err=>{
        failure(err)
    })
}
