<template>
    <div class="formconnect">
        <topbar>
            <div class="save" slot="save">
                <p class="formname"><label for="formname">工作表名称</label><input type="text" v-model="formname" style="background-color:#f0f2f3"></p>
                <p @click="saveconnectform" class="savebutton"><router-link to="/panel" style="text-decoration: none; color: #5182e4;">保存</router-link></p>
             </div>
        </topbar>
        <div class="mainbox">
            <sidebar>
                <span class="sheet" slot="name">工作表</span>
                <div class="item" slot="item" draggable="true" v-for="(item,index) in names" :key="index" :id="item.nv_tb_name" :class="{active:currentName == item.nv_tb_name}" @mousedown="liClick(item.nv_tb_name)" >{{item.nv_tb_name}}</div>
            </sidebar>
            <div id="continer">
                <canvas id="canvas" width="1200" height="900" @click="canvasclick"></canvas>
                <div class="toolbox" :style="{display:toolboxActive.display, top:currentNodeY+'px', left:currentNodeX+'px'}">
                    <div class="tool" @click="deleteclick">删除</div>
                    <div class="tool" @click="showSelect">关联方式</div>
                </div>
                <div class="selectConnectMethod" :style="{display:selectorActive.display, top:currentNodeY+'px', left:currentNodeX+'px'}">
                    <el-dropdown>
                        <span class="el-dropdown-link">
                            关联方式<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <span style="margin-left:20px">{{connectMethod}}</span>
                        <el-dropdown-menu slot="dropdown">
                            <div class="left" @click="connectMethod='left join'"><el-dropdown-item>left join</el-dropdown-item></div>
                            <div class="inner" @click="connectMethod='inner join'"><el-dropdown-item>inner join</el-dropdown-item></div>
                            <div class="full" @click="connectMethod='full join'"><el-dropdown-item>full join</el-dropdown-item></div>
                        </el-dropdown-menu>
                    </el-dropdown>
                    <div class="itemSelect">
                        <span style="width: 45px;">{{fatherNodeName}}</span>
                        <span style="margin-left:120px; width: 45px;">{{currentNodeName}}</span>
                        <div class="itemsmenu" style="margin-top:10px">
                            <el-dropdown>
                                <span class="el-dropdown-link" style="width:50px">
                                    {{leftform}}<i class="el-icon-arrow-down el-icon--right"></i>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <div v-for="item in leftField" :key="item" @click="leftform=item"><el-dropdown-item>{{item}}</el-dropdown-item></div>
                                </el-dropdown-menu>
                            </el-dropdown>
                            <span style="margin-left:30px;">=</span>
                            <el-dropdown>
                                <span class="el-dropdown-link" style="margin-left:30px; width:50px">
                                    {{rightform}}<i class="el-icon-arrow-down el-icon--right"></i>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <div v-for="item in rightField" :key="item" @click="rightform=item"><el-dropdown-item>{{item}}</el-dropdown-item></div>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </div>
                    </div>
                    <input type="submit" value="确定" class="button" style="margin-left:80px" @click="setNodeInfo">
                    <input type="submit" value="取消" class="button">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import store from '../store/index'
    import {request, requestandset} from '../network/request'
    import topbar from '../components/panel/topbar'
    import sidebar from '../components/panel/sidebar'
    export default {
        name:'formconnect',
        data(){
            return{
                //新创建的表名
                formname:'',
                //表名
                names:[],
                //当前选中或正在拖拽的表
                currentName:'',
                fillText:'',
                dragelement:null,
                continer:null,
                //用于toolbox显示控制
                currentNodeX:0,
                currentNodeY:0,
                toolboxActive:{
                    display: 'none',
                },
                //用于selectConnectMethod显示控制
                selectorActive:{
                    display:'none',
                },
                //存储关联方式
                connectMethod:'',
                leftform:'',
                rightform:'',

                currentNodeName:'',
                fatherNodeName:'',

                leftField:[],
                rightField:[],

                //用于遍历的树形结构
                root : {
                    // name:'D盘',
                    // xposition:50,
                    // yposition:200,
                    // addfunction:'',
                    // children:[
                    //     {
                    //     name:'学习',
                    //     xposition:250,
                    //     yposition:250,
                    //     addfunction:'left',//left inner
                    //     },
                    //     {
                    //     name:'电影',
                    //     xposition:250,
                    //     yposition:150,
                    //     addfunction:'left',//left inner
                    //     },
                    //     {
                    //     name:'游戏',
                    //     xposition:250,
                    //     yposition:350,
                    //     addfunction:'left',//left inner
                    //     }
                    // ]
                }
            }
        },

        components:{
            topbar,
            sidebar,
            store,
        },
        
        mounted(){
            request({
                url:'/ws/tbinfo'
            },res=>{
                //success函数
                console.log(res);
                this.names=res.data;
            },err=>{
                //failure函数
                console.log(err);
            })
        },

        methods: {
            liClick(name){
                    var that = this
                    this.currentName = name;
                    this.dragelement = document.getElementById(this.currentName)
                    this.continer = document.getElementById('continer')
                    var canvas = document.querySelector("#canvas")
                    var ctx = canvas.getContext('2d')
                    this.dragelement.ondragend=function(){
                        
                    }
                    this.continer.ondragover=function () {
                        return false;
                    }               
                    this.continer.ondrop=function(e){
                        console.log(e);//获取鼠标抬起位置
                        //绘制箭头函数
                         function drawArrow(ctx, fromX, fromY, toX, toY,theta,headlen,width,color) {
                            
                                theta = typeof(theta) != 'undefined' ? theta : 30;
                                headlen = typeof(theta) != 'undefined' ? headlen : 10;
                                width = typeof(width) != 'undefined' ? width : 1;
                                color = typeof(color) != 'color' ? color : '#000';
                            
                                // 计算各角度和对应的P2,P3坐标
                                var angle = Math.atan2(fromY - toY, fromX - toX) * 180 / Math.PI,
                                    angle1 = (angle + theta) * Math.PI / 180,
                                    angle2 = (angle - theta) * Math.PI / 180,
                                    topX = headlen * Math.cos(angle1),
                                    topY = headlen * Math.sin(angle1),
                                    botX = headlen * Math.cos(angle2),
                                    botY = headlen * Math.sin(angle2);
                            
                                ctx.save();
                                ctx.beginPath();
                            
                                var arrowX = fromX - topX,
                                    arrowY = fromY - topY;
                            
                                ctx.moveTo(arrowX, arrowY);
                                ctx.moveTo(fromX, fromY);
                                ctx.lineTo(toX, toY);
                                arrowX = toX + topX;
                                arrowY = toY + topY;
                                ctx.moveTo(arrowX, arrowY);
                                ctx.lineTo(toX, toY);
                                arrowX = toX + botX;
                                arrowY = toY + botY;
                                ctx.lineTo(arrowX, arrowY);
                                ctx.strokeStyle = color;
                                ctx.fillStyle = color;
                                ctx.lineWidth = width;
                                ctx.fill();
                                ctx.stroke();
                                ctx.restore();
                            }
                         //树的遍历绘制
                         function traverseTree(node) {
                            if (!node) {
                                return;
                            }
                            console.log(node.name);
                            ctx.beginPath();
                            ctx.moveTo(node.xposition,node.yposition)
                            ctx.lineTo(node.xposition,node.yposition+30)
                            ctx.lineTo(node.xposition+100,node.yposition+30)
                            ctx.lineTo(node.xposition+100,node.yposition+10)
                            ctx.lineTo(node.xposition+70,node.yposition)
                            ctx.moveTo(node.xposition+75,node.yposition+15)
                            ctx.lineTo(node.xposition+80,node.yposition+20)
                            ctx.lineTo(node.xposition+85,node.yposition+15)
                            ctx.fillStyle='#5182e4'
                            ctx.fill()
                            ctx.fillStyle='#fff'
                            ctx.front="14px PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif"
                            ctx.fillText(node.name ,node.xposition+5 ,node.yposition+20)
                            ctx.strokeStyle='#fff'
                            ctx.stroke();
                            if (node.children && node.children.length > 0) {
                                drawArrow(ctx, node.xposition+110, node.yposition+20, node.xposition+100, node.yposition+20, 30, 8, 2, '#5182e4');
                                var i = 0;
                                for (i = 0; i < node.children.length; i++) {
                                traverseTree(node.children[i]);
                                ctx.beginPath();
                                ctx.moveTo(node.xposition+110, node.yposition+20);
                                ctx.lineTo(node.children[i].xposition, node.children[i].yposition+20);
                                ctx.strokeStyle="#5182e4";
                                ctx.stroke();
                                }
                            }
                         }
                        
                        ctx.clearRect(0,0,1200,900);

                        requestandset({
                            url:'/mt/ins',
                            method:'post',
                            data: {
                                name:name,
                                xposition:e.offsetX,
                                yposition:e.offsetY,
                                addfunction:'',
                                leftform:'',
                                rightform:'',
                            }
                        },res=>{
                            //success函数
                            console.log(res);
                            that.root = res.data;
                           
                        },res=>{
                            //success函数
                            console.log(res);
                            traverseTree(that.root)
                           
                        },err=>{
                            //failure函数
                            console.log(err);
                        })
                        
                        // traverseTree(that.root)
                    }
                },

            canvasclick(e){
                 var that = this;
                 var found = 0;
                 this.selectorActive.display = 'none';
                 function traverseTree(node) {
                    //  console.log('进入了遍历树函数');
                    console.log(e.offsetX);
                    console.log(e.offsetY);
                            if (!node) {
                                console.log("结束遍历");
                                return;
                            }
                            if (e.offsetX<node.xposition+100&&e.offsetX>node.xposition+70&&e.offsetY<node.yposition+30&&e.offsetY>node.yposition) {
                                console.log("找到节点");
                                that.toolboxActive.display='block';
                                that.currentNodeX=node.xposition+20;
                                that.currentNodeY=node.yposition+30;
                                that.currentNodeName=node.name;
                                if(node.addfunction){
                                    that.connectMethod=node.addfunction;
                                } else{
                                    that.connectMethod='left join';
                                }
                                found=1;
                            }
                            if(found==0){
                                console.log("未找到节点");
                                that.toolboxActive.display='none';
                                that.currentNodeX=0;
                                that.currentNodeY=0;
                            }
                            if (node.children && node.children.length > 0) {
                                var i = 0;
                                for (i = 0; i < node.children.length; i++) {
                                traverseTree(node.children[i]);
                                }
                            }
                         }
                 traverseTree(that.root);
                 console.log(this.currentNodeX);
                 console.log(this.currentNodeY);
            },
        
            deleteclick(){
                var that = this;
                var canvas = document.querySelector("#canvas");
                var ctx = canvas.getContext('2d');
                //绘制箭头函数
                         function drawArrow(ctx, fromX, fromY, toX, toY,theta,headlen,width,color) {
                            
                                theta = typeof(theta) != 'undefined' ? theta : 30;
                                headlen = typeof(theta) != 'undefined' ? headlen : 10;
                                width = typeof(width) != 'undefined' ? width : 1;
                                color = typeof(color) != 'color' ? color : '#000';
                            
                                // 计算各角度和对应的P2,P3坐标
                                var angle = Math.atan2(fromY - toY, fromX - toX) * 180 / Math.PI,
                                    angle1 = (angle + theta) * Math.PI / 180,
                                    angle2 = (angle - theta) * Math.PI / 180,
                                    topX = headlen * Math.cos(angle1),
                                    topY = headlen * Math.sin(angle1),
                                    botX = headlen * Math.cos(angle2),
                                    botY = headlen * Math.sin(angle2);
                            
                                ctx.save();
                                ctx.beginPath();
                            
                                var arrowX = fromX - topX,
                                    arrowY = fromY - topY;
                            
                                ctx.moveTo(arrowX, arrowY);
                                ctx.moveTo(fromX, fromY);
                                ctx.lineTo(toX, toY);
                                arrowX = toX + topX;
                                arrowY = toY + topY;
                                ctx.moveTo(arrowX, arrowY);
                                ctx.lineTo(toX, toY);
                                arrowX = toX + botX;
                                arrowY = toY + botY;
                                ctx.lineTo(arrowX, arrowY);
                                ctx.strokeStyle = color;
                                ctx.fillStyle = color;
                                ctx.lineWidth = width;
                                ctx.fill();
                                ctx.stroke();
                                ctx.restore();
                            }
                         //树的遍历绘制
                         function traverseTree(node) {
                            if (!node) {
                                return;
                            }
                            console.log(node.name);
                            ctx.beginPath();
                            ctx.moveTo(node.xposition,node.yposition)
                            ctx.lineTo(node.xposition,node.yposition+30)
                            ctx.lineTo(node.xposition+100,node.yposition+30)
                            ctx.lineTo(node.xposition+100,node.yposition+10)
                            ctx.lineTo(node.xposition+70,node.yposition)
                            ctx.moveTo(node.xposition+75,node.yposition+15)
                            ctx.lineTo(node.xposition+80,node.yposition+20)
                            ctx.lineTo(node.xposition+85,node.yposition+15)
                            ctx.fillStyle='#5182e4'
                            ctx.fill()
                            ctx.fillStyle='#fff'
                            ctx.front="14px PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif"
                            ctx.fillText(node.name ,node.xposition+5 ,node.yposition+20)
                            ctx.strokeStyle='#fff'
                            ctx.stroke();
                            if (node.children && node.children.length > 0) {
                                drawArrow(ctx, node.xposition+110, node.yposition+20, node.xposition+100, node.yposition+20, 30, 8, 2, '#5182e4');
                                var i = 0;
                                for (i = 0; i < node.children.length; i++) {
                                traverseTree(node.children[i]);
                                ctx.beginPath();
                                ctx.moveTo(node.xposition+110, node.yposition+20);
                                ctx.lineTo(node.children[i].xposition, node.children[i].yposition+20);
                                ctx.strokeStyle="#5182e4";
                                ctx.stroke();
                                }
                            }
                         }
                        
                        ctx.clearRect(0,0,1200,900);

                        requestandset({
                            url:'/mt/delete',
                            method:'post',
                            data: {
                                name:that.currentName,
                                xposition:that.currentNodeX-20,
                                yposition:that.currentNodeY-30,
                                addfunction:'',
                                leftform:'',
                                rightform:'',
                            }
                        },res=>{
                            //success函数
                            console.log(res);
                            that.root = res.data;
                           
                        },res=>{
                            //success函数
                            console.log(res);
                            traverseTree(that.root)
                            that.currentNodeX=0;
                            that.currentNodeY=0;
                            that.toolboxActive.display='none';
                           
                        },err=>{
                            //failure函数
                            console.log(err);
                        })
            },

            showSelect(){
                this.selectorActive.display = 'block';
                var that = this;
                var found = 0;
                function traverseTree(node) {
                    //  console.log('进入了遍历树函数');
                            if (!node) {
                                console.log("结束遍历");
                                return;
                            }
                            if (node.children && node.children.length > 0) {
                                if(!found){
                                   for (let index = 0; index < node.children.length; index++) {
                                       if (node.children[index].name==that.currentNodeName) {
                                           that.fatherNodeName=node.name;
                                           found=1;
                                       }
                                   }
                            }
                                var i = 0;
                                for (i = 0; i < node.children.length; i++) {
                                traverseTree(node.children[i]);
                                }
                            }
                         }
                 traverseTree(that.root);
                 request({
                    url:'/mt/show_',
                    method:'post',
                    data:{
                        xposition:that.currentNodeX-20,
                        yposition:that.currentNodeY-30,
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    if(res.data.addfunction){
                        this.connectMethod=res.data.addfunction
                    }else{
                        this.connectMethod='left join'
                    }
                    if(res.data.leftform){
                        this.leftform=res.data.leftform
                    }else{
                        this.leftform=this.leftField[0]
                    }
                    if(res.data.rightform){
                        this.rightform=res.data.rightform
                    }else{
                        this.rightform=this.rightField[0]
                    }
                },err=>{
                    //failure函数
                    console.log(err);
                })

                request({
                    url:'/mt/field',
                    params:{
                        leftTable:this.currentNodeName,
                        rightTable:this.fatherNodeName
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    this.leftField=res.data.leftField;
                    this.rightField=res.data.rightField
                },err=>{
                    //failure函数
                    console.log(err);
                })
            },

            setNodeInfo(){
                this.selectorActive.display='none'
                 request({
                    url:'/mt/revise_',
                    method:'post',
                    data:{
                        xposition:this.currentNodeX-20,
                        yposition:this.currentNodeY-30,
                        addfunction:this.connectMethod,
                        leftform:this.leftform,
                        rightform:this.rightform
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    this.root=res.data;
                },err=>{
                    //failure函数
                    console.log(err);
                })
            },

            saveconnectform(){
                request({
                    url:'/mt/save',
                    method:'post',
                    data:{
                        name:this.formname,
                        map:this.root
                    }
                },res=>{
                    //success函数
                    console.log(res);
                },err=>{
                    //failure函数
                    console.log(err);
                })
                window.location.reload();
            },
        },
    }
</script>
<style>
    .mainbox{
        display: flex;
        flex-direction: row;
        width: 100%;
        height: 100%;
    }

    #continer{
        position: relative;
    }

    .formconnect .formname{
        display: inline-block;
        width: 270px;
        color: rgba(10,18,32,.64);
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 13px;
        font-weight: 400;
    }

    .toolbox{
        position: absolute;
        background-color: #FFF;
        box-shadow: 0 0 6px 0 rgba(0,0,0,.1), 0 10px 12px 0 rgba(170,182,206,.36);
        /* width: 80px; */
    }
    
    .tool{
        line-height: 32px;
        display: block;
        height: 32px;
        text-align: center;
        cursor: pointer;
        padding: 0 16px;
        white-space: nowrap;
        color: rgba(10,18,32,.64);
    }

    .tool:hover{
        border-radius: 2px;
        background: rgba(128,133,144,.15);
    }

    .selectConnectMethod{
        background-color: #fff;
        width: 220px;
        height: 150px;
        position: absolute;
        top: 0;
        left: 0;
        box-shadow: 0 0 10px 0 rgba(0,0,0,.12), 0 10px 12px 0 rgba(118,153,222,.1);
        padding: 20px;
    }

    .formconnect{
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 1001;
    }

    .formconnect .item{
        width: 100%;
        height: 40px;
    }

    .formconnect .save{
        margin-left: 5rem;
    }

    .formconnect .savebutton{
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 14px;
        display: inline-block;
        margin: 0;
        padding: 5px;
    }
    
    .formconnect .savebutton:hover{
        background-color:rgba(81,130,228,0.1);
        transition: 0.7s all;
    }

    .active{
        background-color: #fff;
    }

    .formconnect .item{
        cursor: pointer;
        height: 1.25rem;
        line-height: 1.25rem;
        width: 100%;
        padding: 10px 0;
        padding-left: 25px;
        margin: 0;
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 12px;
        font-weight: 600;
        color: rgba(10,18,32,.64);
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-rendering: optimizelegibility;
    }

    /* 下拉菜单样式 */
    .el-dropdown-link {
        cursor: pointer;
        color: #409EFF;
    }
    .el-icon-arrow-down {
        font-size: 12px;
    }

    .itemSelect{
        margin-top: 20px;
        padding-top: 20px;
        border-top: 1px solid rgba(0,0,0,.12);
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 14px;
        color: rgba(10,18,32,.64);
        font-weight: 400;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-rendering: optimizelegibility;
    }

    .formconnect .button{
        width: 70px;
        height: 30px;
        border-left-width: 0;
        border-top-width: 0;
        border-right-width: 0;
        border-bottom-width: 0;
        background-color: #fff;
        color: #5182e4;
        font-weight: 700;
        cursor: pointer;
        margin: 0;
        padding: 0;
        margin-top: 20px;
    }

    .formconnect .button:hover{
        background-color:rgba(81,130,228,0.1);
    }

    .formconnect span{
        white-space: nowrap;  /*强制span不换行*/
        display: inline-block;  /*将span当做块级元素对待*/
        overflow: hidden;  /*超出宽度部分隐藏*/
        text-overflow: ellipsis;  /*超出部分以点号代替*/
    }
</style>