<template>
    <div class="logindatabase">
        <div class="databasetag">
            <slot name="img"></slot>
            <div class="databaseinfo">
                <slot name="dbname"></slot>
                <slot name="dbinfo"></slot>
            </div>
        </div>
        <div class="logintag" v-if="showstep==1">
            <span class="steptagactive">1</span>
            <span class="stepmesgactive"> 连接配置</span>
            <span class="stepline"></span>
            <span class="steptag">2</span>
            <span class="stepmesg"> 数据表配置</span>
            <span class="stepline"></span>
            <span class="steptag">3</span>
            <span class="stepmesg"> 高级配置</span>
        </div>
        <div class="logintag" v-if="showstep==2">
            <span class="steptag">1</span>
            <span class="stepmesg"> 连接配置</span>
            <span class="stepline"></span>
            <span class="steptagactive">2</span>
            <span class="stepmesgactive"> 数据表配置</span>
            <span class="stepline"></span>
            <span class="steptag">3</span>
            <span class="stepmesg"> 高级配置</span>
        </div>
        <div class="logintag" v-if="showstep==3">
            <span class="steptag">1</span>
            <span class="stepmesg"> 连接配置</span>
            <span class="stepline"></span>
            <span class="steptag">2</span>
            <span class="stepmesg"> 数据表配置</span>
            <span class="stepline"></span>
            <span class="steptagactive">3</span>
            <span class="stepmesgactive"> 高级配置</span>
        </div>
        <div class="loginform" v-if="showstep==1">
            <div class="leftform">
                <p><slot name="loginform1"></slot><input type="text" v-model="formMess.host"><input type="text" placeholder="端口" class="portinput"  v-model="formMess.port"></p>
                <p><slot name="loginform2"></slot><input type="text" v-model="formMess.username"></p>
                <p><slot name="loginform3"></slot><input type="text" class="password" v-model="formMess.password"></p>
                <p><slot name="loginform4"></slot><input type="text" class="password"  v-model="formMess.database"></p>
                <input type="submit" value="下一步" class="next" @click="showstep2">
            </div>
            <div class="rightform"></div>
        </div>
        <div class="loginform" v-if="showstep==2">
            <div class="leftform">
                <div class="checkboxform">
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                    <div style="margin: 15px 0;"></div>
                    <el-checkbox-group v-model="check_tables" @change="handleCheckedTablesChange" class="checkboxgroup">
                        <el-checkbox v-for="table in tables" :label="table" :key="table.TABLE_NAME">{{table.TABLE_NAME}}</el-checkbox>
                    </el-checkbox-group>
                </div>
                <input type="submit" value="上一步" class="front" @click="showstep--">
                <input type="submit" value="下一步" class="next" @click="showstep++">
            </div>
            <div class="rightform"></div>
        </div>
        <div class="loginform" v-if="showstep==3">
            <div class="leftform">
                <p><slot name="loginform5"></slot><input type="text" v-model="name"></p>
                <input type="submit" value="上一步" class="front" @click="showstep--">
                <div @click="finish"><router-link tag="div" to="/database"><input type="submit" value="完成" class="next"></router-link></div>
            </div>
            <div class="rightform"></div>
        </div>
    </div>
</template>
<script>
    import {request} from '../../network/request'
    import store from '../../store/index'

    export default {
        name:'logindatabase',
        data(){
            return{
                showstep:1,
                formMess:{
                    'host':'',
                    'port':'',
                    'username':'',
                    'password':'',
                    'database':'',
                    'db_type':'1',
                },

                checkAll: false,
                check_tables: [],
                tables: [],
                name:'',
                isIndeterminate: true
            }
        },
        components:{
            store,
        },
        methods: {
            showstep2(){
                this.showstep++;
                // let formData = new FormData();
                // for(var key in this.formMess){
                //     formData.append(key,this.formMess[key]);
                // }

                request({
                    url:'/ds/conn',
                    method:'post',
                    data:this.formMess,
                },res=>{
                    //success函数
                    console.log(res);
                   for(var key in res.data){
                        this.tables.push(res.data[key])
                    }
                },err=>{
                    //failure函数
                    console.log(err);
                })
            },
            finish(){
                this.$store.commit('showview')
                this.formMess['name']=this.name
                this.formMess['check_tables']=this.check_tables
                console.log(this.formMess);
                request({
                    url:'/ds/create',
                    method:'post',
                    data:this.formMess,
                },res=>{
                    //success函数
                    console.log(res);
                },err=>{
                    //failure函数
                    console.log(err);
                })
            },
            handleCheckAllChange(val) {
                this.check_tables = val ? this.tables : [];
                this.isIndeterminate = false;
            },
            handleCheckedTablesChange(value) {
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.tables.length;
                this.isIndeterminate = checkedCount > 0 && checkedCount < this.tables.length;
            }
       },
    }
</script>

<style>
    .logindatabase{
        margin-top: 1.25rem;
    }

    .databasetag{
        display: flex;
        flex-direction: row;
        background-color: #fff;
    }
    
    .logintag{
        display: flex;
        align-items : center; 
        margin-top: 1.25rem;
    }

    .steptagactive{
        display: inline-block;
        width: 20px;
        font-size: 15px;
        background-color: #5182e4;
        border-radius:21px;
        text-align: center;
        color: #fff;
        margin-right: 5px;
    }

    .steptag{
        display: inline-block;
        width: 20px;
        font-size: 15px;
        background-color: rgb(83, 131, 228, 0.5);
        border-radius:20px;
        text-align: center;
        color: #fff;
        margin-right: 5px;
    }

    .stepline{
        display: inline-block;
        width: 3.125rem;
        height: 1px;
        background-color: rgb(83, 131, 228, 0.5);
        margin: 0 10px 0;
    }

    .stepmesg{
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 13px;
        color: rgb(0, 0, 0, 0.5);
    }

    .stepmesgactive{
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 13px;
    }

    .loginform{
        margin-top: 15px;
        width: 100%;
        display: flex;
        flex-direction: row;
    }

    .loginform label{
        display: block;
    }

    .leftform{
        width: 40%;
        height: 14.375rem;
        background-color: #fff;
        padding-left: 1.25rem;
        position: relative;
    }

    input{
        outline-style: none;
        border-left-width: 0;
        border-top-width: 0;
        border-right-width: 0;
        border-color :  #aaa;
        border-bottom-width: 1px;
        margin-left: 15px;
        padding: 3px;
    }

    input:focus{
        border-bottom-width: 2px;
        border-color: #5597fa;
        outline: 0;
        /* -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6) */
    }

    .portinput{
        width: 45px;
        margin-left: 10px;
    }

    .logindatabase .front{
        position: absolute;
        left: 0;
        bottom: 0;
        margin-bottom: 20px;
        margin-left: 20px;
        width: 70px;
        height: 30px;
        border-left-width: 0;
        border-top-width: 0;
        border-right-width: 0;
        border-bottom-width: 0;
        background-color: #fff;
        color: #5182e4;
        font-weight: 700;
        cursor: pointer;
    }

    .logindatabase .front:hover{
        background-color:rgba(81,130,228,0.1);
    }

    .logindatabase .next{
        position: absolute;
        right: 0;
        bottom: 0;
        margin-bottom: 20px;
        margin-right: 20px;
        width: 70px;
        height: 30px;
        border-left-width: 0;
        border-top-width: 0;
        border-right-width: 0;
        border-bottom-width: 0;
        background-color: #fff;
        color: #5182e4;
        font-weight: 700;
        cursor: pointer;
    }

    .logindatabase .next:hover{
        background-color:rgba(81,130,228,0.1);
    }

    .logindatabase a{
        position: absolute;
        right: 0;
        bottom: 0;
        margin-bottom: 20px;
        margin-right: 20px;
        width: 70px;
        height: 30px;
        background-color: #fff;
        text-decoration: none;
        color: #5182e4;
        font-weight: 700;
        cursor: pointer;
    }

    .rightform{
        width: 60%;
        background-color: #f8f8f8;
    }

    .checkboxform{
        height: 180px;
        overflow: auto;
    }
</style>