<template>
    <div class="navbar">
      <div class="item" id="logo"></div>
      <ul class="menu">
          <li @click="boardclick"><router-link to="/board" :class="{ 'active': boardActive }">仪表盘</router-link></li>
          <li @click="panelclick"><router-link to="/panel" :class="{ 'active': panelActive }">工作表</router-link></li>
          <li @click="dbclick"><router-link to="/database" :class="{ 'active': databaseActive }">数据源</router-link></li>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'navbar',
  data(){
	  return{
		  boardActive:false,
		  panelActive:false,
		  databaseActive:false,
	  }
  },
  methods: {
	  boardclick(){
		  this.boardActive=true,
		  this.panelActive=false,
		  this.databaseActive=false
	  },
	  panelclick(){
		  this.boardActive=false,
		  this.panelActive=true,
		  this.databaseActive=false
		  this.$store.state.tableData.splice(0)
          this.$store.state.lineName.splice(0)
	  },
	  dbclick(){
		  this.boardActive=false,
		  this.panelActive=false,
		  this.databaseActive=true
	  },
  }
}
</script>

