<template>
  <div class="panel" ref="panel">
    <div class="box">
      <sidebar class="sidebar" ref="sidebar">
        <span class="sheet" slot="name">工作表</span>
        <p v-for= "item in boardname" 
        :key="item.nv_tb_name" class="item" 
        slot="item" 
        :class="{active:currentName == item.nv_tb_name}" 
        @mousedown="liClick(item.nv_tb_name)">
            {{item.nv_tb_name}}
            <i class="fa fa-download" aria-hidden="true" @click="download"></i> 
        </p>
      </sidebar>

    <div class="separator" ref="separator">
        <div class="separatoritem"></div>
    </div>
  </div>
    <div class="mainpanel">
        <div class="title">
          <span class="panelname">{{this.currentName}}</span>
          <div class="formConnectLink">
            <router-link to="/panel/formconnect"><i class="fa fa-files-o" aria-hidden="true"></i>多表关联</router-link>
          </div>
          <div class="formConnectLink">
              <router-link to="/panel/alert"><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> 数据预警</router-link>
          </div>
        </div>
      <panelform class="panelform"></panelform>
    </div>
  </div>
</template>

<script>
import sidebar from '../components/panel/sidebar'
import panelform from'../components/panel/panelform'
import alert from '../components/panel/alertmask'
import {request,requestandset} from '../network/request'
import axios from 'axios'
import store from '../store/index'
export default {
  name: 'panel',
  data(){
    return{
      currentName:'仪表盘',
      boardname:[],
    }
  },
  components:{
      sidebar,
      panelform,
      alert,
      store,
  },

  created(){
      request({
        url:'/ws/tbinfo',
      },res=>{
          //success函数
          console.log(res);
          this.boardname=res.data;
      },err=>{
          //failure函数
          console.log(err);
      })
  },

  mounted () {
      this.dragControllerDiv();
    },
    methods: {
      dragControllerDiv: function () {
        var resize = document.getElementsByClassName('separator');
        var left = document.getElementsByClassName('sidebar');
        var mid = document.getElementsByClassName('graph');
        var box = document.getElementsByClassName('panel');
        for (let i = 0; i < resize.length; i++) {
          // 鼠标按下事件
          resize[i].onmousedown = function (e) {
            var startX = e.clientX;
            resize[i].left = resize[i].offsetLeft;
            // 鼠标拖动事件
            document.onmousemove = function (e) {
              var endX = e.clientX;
              var moveLen = resize[i].left + (endX - startX); // （endx-startx）=移动的距离。resize[i].left+移动的距离=左边区域最后的宽度
              var maxT = box[i].clientWidth - resize[i].offsetWidth;  // 容器宽度 - 左边区域的宽度 = 右边区域的宽度
 
              if (moveLen < 200) moveLen = 200; // 左边区域的最小宽度为150px
              if (moveLen > maxT - 200) moveLen = maxT  - 200;  //右边区域最小宽度为150px
 
              resize[i].style.left = moveLen; // 设置左侧区域的宽度
 
              for (let j = 0; j < left.length; j++) {
                left[j].style.width = moveLen + 'px';
                mid[j].style.width = (box[i].clientWidth - moveLen - 10) + 'px';
              }
            }
            // 鼠标松开事件
            document.onmouseup = function (evt) {
              document.onmousemove = null;
              document.onmouseup = null;
              resize[i].releaseCapture && resize[i].releaseCapture(); //当你不在需要继续获得鼠标消息就要应该调用ReleaseCapture()释放掉
            }
            resize[i].setCapture && resize[i].setCapture(); //该函数在属于当前线程的指定窗口里设置鼠标捕获
            return false;
          }
        }
 
      },

      liClick(name){
        this.currentName = name;
        this.$store.state.currentName =name;
        this.$store.state.checkedTable=name;
        this.$store.state.tableData.splice(0)
        this.$store.state.lineName.splice(0)
        requestandset({
        url:'/ws/fields',
        params:{
          checkedTable:this.currentName,
          page:0
        },
      },res=>{
          //success函数
          // console.log(res);
          this.$store.state.tableData=res.data.value;
          this.$store.state.lineName=res.data.name;
          this.$store.state.warnrow=res.data.row;
      },res=>{
          if (this.$store.state.warnrow.length) {
            this.$notify({
              title: '警告',
              message: '预警TEST已被触发',
              type: 'warning'
            });
          }
      },err=>{
          //failure函数
          console.log(err);
      })
      },
    
      download(){
        axios({
            method: 'GET',
            url: 'http://47.102.146.116:8080/ex/download',
            params: {checkedTable: this.currentName},
            responseType: 'blob'
        }).then(res=>{
            console.log(res)
            let blob = new Blob([res.data], {type: "application/vnd.ms-excel"});
            let url = window.URL.createObjectURL(blob);
            window.location.href = url;
        }).catch(err=>{
            console.log(err)
        })
      }
    }

}
</script>

<style>
  .mainpanel{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: rgba(128,133,144,.15);
  }

  .panelname{
    display: inline-block;
    font-size: 22px;
    font-family: Roboto,'PingFang SC',sans-serif!important;
    color: rgba(10,18,32,.64);
    margin: 30px;
  }

  .panelform{
    flex: 1;
  }

  .fa-files-o{
    color: #5182E4;
    margin-right:5px;
  }

  .fa-exclamation-triangle{
    color: #5182E4;
    margin-right:5px;
  }

  .sheet{
    display: inline-block;
    font-size: 12px;
		font-weight: 600;
		font-family: sans-serif;
    color: #a1a1a1;
  }

  .sheet{
    padding: 5px;
  }

  .panel .item{
    cursor: pointer;
    height: 1.25rem;
    line-height: 1.25rem;
    width: 81%;
    padding: 10px 0;
    padding-left: 25px;
    margin: 0;
    font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
    font-size: 12px;
    font-weight: 600;
    color: rgba(10,18,32,.64);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizelegibility;
  }

  .panel .title{
    display: flex;
    flex-direction: row;
    padding-top: 10px;
  }

  .panel .formConnectLink{
    position: relative;
    width: 80px;
    cursor: pointer;
    margin: 35px;
  }

  .panel a{
    text-decoration: none;
    color: rgba(10,18,32,.44);
    font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
    font-size: 14px;
    font-weight: 700;
  }

  .panel a:hover{
    color: rgba(10,18,32,.64);
  }

  .panel .formConnectLink:before{
			content: '';
			position: absolute;
			width: 0;
			left: 50%;
      top: 23px;
			bottom: 0;
			height: 1px;
			background-color: #5182E4;
		}

		.panel .formConnectLink:hover:before{
			width: 100%;
			left: 0%;
			transition: all 0.5s;
		}

    .active{
        background-color: #fff;
    }

    .fa-download{
      display: block;
      float: right;
      color: #5182E4;
      margin-right: 5px;
    }
</style>