<template>
    <div class="alertmask">
        <div class="dialog">
            <p style="margin: 0">预警设置</p>
            <div v-if="!showStep" class="step_1">
                <span style="color: rgba(10,18,32,.46); font-size: 11px;">预警列表</span>
                <span class="addalertbtn" @click="showStep=1">添加预警</span>
               <div class="showTask">
                   <el-table
                    :data="warnList"
                    style="width: 100%">
                    <el-table-column
                    label="预警名称"
                    width="180">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 10px">{{ scope.row.warnName }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="状态"
                    width="180">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top">
                        <p>预警名称: {{ scope.row.warnName }}</p>
                        <p>数据表: {{ scope.row.tableName }}</p>
                        <p>预警条件：{{scope.row.field}}{{scope.row.type}}{{scope.row.value}}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.active }}</el-tag> 
                        </div>
                        </el-popover>
                    </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <!-- <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button> -->
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                    </el-table-column>
                </el-table>
               </div>
               <span @click="clearform"><router-link to="/panel" class="next" style="right: 50px;">确定</router-link></span>
               <span @click="clearform"><router-link to="/panel" class="next">取消</router-link></span>
            </div>
            <div v-if="showStep" style="width: 450px;height: 300px; overflow-y:auto; overflow-x:hidden; margin-left: 25px">
                <span style="display: inline-block; color: rgba(10,18,32,.46); font-size: 14px; margin-top: 10px; font-weight: 700;">预警名称</span>
                <input type="text" placeholder="请输入预警名称" style="background-color: #F0F2F3;" v-model="warningName">
                <p style="color: rgba(10,18,32,.46); font-size: 14px; margin-top: 10px; font-weight: 700;">条件设置</p>
               <div class="showTask">
                   <div v-for="count in factor" :key="count" style="margin-top: 10px;">
                       <el-dropdown>
                            <span class="el-dropdown-link">
                                 <input type="text" placeholder="请选择" class="dropdown" :value="dropdownlist_1[count-1]"><i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <div v-for="Id in IdList" :key="Id" @click="setList_1(count, Id)"><el-dropdown-item>{{Id}}</el-dropdown-item></div>
                            </el-dropdown-menu>
                        </el-dropdown>
                        <el-dropdown>
                            <span class="el-dropdown-link">
                                <input type="text" placeholder="请选择" class="dropdown" :value="dropdownlist_2[count-1]"><i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                                <div v-for="item in computeMethod" :key="item" @click="setList_2(count, item)"><el-dropdown-item>{{item}}</el-dropdown-item></div>
                            </el-dropdown-menu>
                        </el-dropdown>
                        <input type="text" placeholder="输入值" v-model="input[count-1]">
                       <i class="el-icon-plus" style="margin-left: 25px;" @click="factor++"></i>
                       <i class="el-icon-minus" style="margin-left: 10px;"  @click="factor--"></i>
                   </div>
                   <div @click="addWarning(); showWarning()"><router-link to="/panel/alert" class="next" style="right: 50px;">确定</router-link></div>
                    <router-link to="/panel" class="next">取消</router-link>
               </div>
            </div>
        </div>
    </div>
</template>
<script> 
    import store from '../../store/index'
    import {request} from '../../network/request'
    export default {
        name:'alertmask',
        data(){
            return{
                showStep:0,
                factor:1,
                warningName:'',
                dropdownlist_1:[],
                dropdownlist_2:[],
                input:[],
                IdList:[],
                computeMethod:['大于','小于','等于','大于等于','小于等于','不等于','为空'],
                warnList:[],
                warnList_temp:[],
            }
        },

        created(){
            request({
                url:'/wa/show',
            },res=>{
                //success函数
                console.log(res);
                this.warnList=res.data;
            },err=>{
                //failure函数
                console.log(err);
            })
            request({
                url:'/wa/field',
                params:{
                    tableName:this.$store.state.checkedTable
                },
            },res=>{
                //success函数
                console.log(res);
                this.IdList=res.data;
            },err=>{
                //failure函数
                console.log(err);
            })
        },
        components:{
            store,
        },
        
        methods: {
            setList_1(count, Id){
                this.$set(this.dropdownlist_1, count-1, Id);
            },
            setList_2(count, item){
                this.$set(this.dropdownlist_2, count-1, item);
            },
            clearform(){
                this.$store.state.tableData.splice(0)
                this.$store.state.lineName.splice(0)
            },
            addWarning(){
                this.showStep=0;
                request({
                url:'/wa/add',
                method:'post',
                data:{
                    tableName:this.$store.state.checkedTable,
                    warnName:this.warningName,
                    warnMap:{
                        fieldName:this.dropdownlist_1,
                        type:this.dropdownlist_2,
                        value:this.input
                    }
                }
            },res=>{
                //success函数
                console.log(res);
                request({
                    url:'/wa/show',
                },res=>{
                    //success函数
                    console.log(res);
                    // this.warnList_temp=res.data;
                    this.warnList=res.data;
                },err=>{
                    //failure函数
                    console.log(err);
                })
            },err=>{
                //failure函数
                console.log(err);
            })
            },

            // showWarning(){
            //     // this.warnList.splice(0,this.warnList.length); 
            //     console.log(this.warnList);
            //     request({
            //         url:'/wa/show',
            //     },res=>{
            //         //success函数
            //         console.log(res);
            //         // this.warnList_temp=res.data;
            //         this.warnList=res.data;
            //     },err=>{
            //         //failure函数
            //         console.log(err);
            //     })
            //     // this.warnList.push(this.warnList_temp);
            //     // console.log(this.warnList);
            // },
        
            handleDelete(index, row) {
                this.warnList.splice(index,1);
                request({
                    url:'/wa/delete',
                    params:{
                     warnName:row.warnName
                    }
                },res=>{
                    //success函数
                    console.log(res);
                    this.warnList=res.data;
                },err=>{
                    //failure函数
                    console.log(err);
                })
            },
        },
    }
</script>
<style>
    .alertmask{
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        background: rgba(213,217,220,.94);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .alertmask .dialog{
        background: #F0F2F3;
        padding: 20px;
        border-radius: 2px;
        width: 500px;
        height: 330px;
        margin: 0;
        position: relative;
    }

    .worksheetselect{
        display: block;
    }

    .alertmask .next{
        text-decoration: none;
        color: #5182e4;
        font-weight: 550;
        font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
        font-size: 15px;
        padding: 5px;
        position: absolute;
        bottom: 0;
        right: 0;
    }
    .alertmask .next:hover{
        background:rgba(81,130,228,0.1);
    }

    .alertmask .step_1{
        width: 450px; 
        height: 300px; 
        position: absolute;
        top: 50px;
        left: 40px;
        padding: 0;
    }

    .alertmask .addalertbtn{
        display: inline-block;
        color: #FFF;
        background: #5182E4;
        padding: 0 8px;
        line-height: 28px;
        font-size: 12px;
        border-radius: 2px;
        margin-left: 338px;
        cursor: pointer;
    }

    .alertmask .showTask{
        width: 450px; 
        height: 210px; 
        overflow-x: hidden;
        overflow-y: auto;
        margin: 0;
        background-color: #fff;
        box-shadow: 0 0 6px 0 rgba(0,0,0,.06), 0 10px 12px 0 rgba(170,182,206,.15);
    }

    .alertmask .dropdown{
        width: 40px;
    }

    .el-dropdown {
        vertical-align: top;
        margin-left: 15px;
    }
    .el-dropdown + .el-dropdown {
        margin-left: 15px;
    }
</style>