<template>
    <div class="dbview">
        <div class="dbtag">
            <span class="title">数据源</span>
            <span @click="adddatabase" class="adddatabase"><i class="fa fa-database" aria-hidden="true"></i>添加数据源</span>
        </div>
        <div class="dbinfo">
          <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column label="名称">
              <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.nv_db_name }}</span>
              </template>
            </el-table-column>
            <el-table-column  label="类型">
              <template slot-scope="scope">
                <img src="../../../public/img/oracle.png" alt="oracle" v-show="scope.row.db_type=='1'">
                <img src="../../../public/img/mySQL.png" alt="mySQL" v-show="scope.row.db_type=='2'">
              </template>
            </el-table-column>
            <el-table-column label="操作" width="180">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- <table border="1">
            <tr>
              <td>名称</td>
              <td>类型</td>
              <td>状态</td>
            </tr>
            <tr v-for="item in tableData" :key="item.nv_db_name">
              <td>{{item.nv_db_name}}</td>
              <td>
                <img src="../../../public/img/oracle.png" alt="oracle" v-show="item.db_type=='1'">
                <img src="../../../public/img/mySQL.png" alt="mySQL" v-show="item.db_type=='2'">
              </td>
              <td>同步完成</td>
            </tr>
          </table> -->
        </div>
  </div>
</template>

<script>
import store from '../../store/index'
import {request} from '../../network/request';
export default {
  name: 'dbselect',
  data() {
      return {
        tableData: [
          // {
        //   db_type:1,
        //   nv_db_name: '1',
        // }, {
        //   db_type:1,
        //   nv_db_name: '2',
        // }, {
        //   db_type:1,
        //   nv_db_name: '3',
        // }, {
        //   db_type:1,
        //   nv_db_name: '4',
        // }
        ]
      }
    },
  components:{
    store,
  },
  created(){
  },
  mounted(){
      var that =this;
      setTimeout(function(){
      request({
          url:'/dspage/names'
      },res=>{
          //success函数
          console.log(res);
          if (res.data.length) {
            that.$store.commit('showview');
          }
          for (let index = 0; index < res.data.length; index++) {
            that.tableData.push(res.data[index]);
          }
      },err=>{
          //failure函数
          console.log(err);
      })},1000)
      
  },
  methods:{
    adddatabase(){
      this.$store.commit('showselect')
      this.$store.commit('visitedcontrol')
    },
    handleDelete(index, row){
      console.log(row);
      this.tableData.splice(index, 1)
      request({
          url:'/delete/ds',
          params:{
            dsName:row.nv_db_name
          }
      },res=>{
          //success函数
          console.log(res);
      },err=>{
          //failure函数
          console.log(err);
      })
    }
  }
}
</script>

<style>
  .dbview .title{
    display: inline-block;
    font-size: 22px;
    font-family: Roboto,'PingFang SC',sans-serif!important;
    margin: 30px;
    color: rgba(10,18,32,.64);
  }

  .dbview .adddatabase{
    display: inline-block;
    position: relative;
    width: 95px;
    margin-left: 40px;
    cursor: pointer;
    color: rgba(10,18,32,.44);
    font-family: 'PingFang SC','Helvetica Neue',Helvetica,STHeitiSC-Light,WOL_SB,'Segoe UI Semibold','Segoe UI',Tahoma,Helvetica,sans-serif;
    font-size: 15px;
    font-weight: 600;
  }

  .fa-database{
    color: #5182E4;
    margin-right:5px;
  }

  .dbview .adddatabase:hover{
    color: rgba(10,18,32,.64);
  }

  .dbview .adddatabase:before{
			content: '';
			position: absolute;
			width: 0;
			left: 50%;
      top: 23px;
			bottom: 0;
			height: 1px;
			background-color: #5182E4;
		}

		.dbview .adddatabase:hover:before{
			width: 100%;
			left: 0%;
			transition: all 0.5s;
		}

    .dbview .dbinfo{
      width: 100%;
      height: 100%;
      overflow: auto;
    }

     .dbview table,.dbview table tr th, .dbview table tr td { border:1px solid #e1e2e3; }
     .dbview table { width: 100%; min-height: 50px; line-height: 50px; text-align: center; border-collapse: collapse; background-color: #fff; border-radius: 2px;}   

    .dbview img{
      width: 75px;
      height: 50px;
    }
</style>